"use client"

import React, { createContext, useContext, useState, ReactNode, useEffect } from "react"

interface User {
  id: string
  username: string
  email: string
  profilePhoto?: string
  bio?: string
  qrCodeData?: string
  friends: string[]
}

interface AuthContextType {
  user: User | null
  login: (email: string, password: string) => Promise<boolean>
  logout: () => void
  updateProfile: (data: Partial<User>) => void
  addFriend: (friendId: string) => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null)

  useEffect(() => {
    // Auto-login for specific user
    const autoLoginEmail = "seppe.taeleman@gmail.com"
    const autoLoginPassword = "Bonijntje2006"

    async function autoLogin() {
      if (!user) {
        const res = await fetch('/api/auth/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ email: autoLoginEmail, password: autoLoginPassword }),
        })
        if (res.ok) {
          const data = await res.json()
          setUser(data.user)
        }
      }
    }
    autoLogin()
  }, [])

  const login = async (email: string, password: string): Promise<boolean> => {
    try {
      const res = await fetch('/api/auth/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
      })
      if (!res.ok) {
        return false
      }
      const data = await res.json()
      setUser(data.user)
      return true
    } catch (error) {
      return false
    }
  }

  const logout = () => {
    setUser(null)
  }

  const updateProfile = (data: Partial<User>) => {
    if (user) {
      setUser({ ...user, ...data })
    }
  }

  const addFriend = (friendId: string) => {
    if (user && !user.friends.includes(friendId)) {
      setUser({ ...user, friends: [...user.friends, friendId] })
    }
  }

  return (
    <AuthContext.Provider value={{ user, login, logout, updateProfile, addFriend }}>
      {children}
    </AuthContext.Provider>
  )
}

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
