'use client'

import React from 'react'

interface FooterProps {
  activeTab: string
  onTabChange: (tab: string) => void
}

const tabs = [
  { id: 'map', label: 'Map' },
  { id: 'clubs', label: 'Clubs' },
  { id: 'events', label: 'Events' },
  { id: 'friends', label: 'Friends' },
]

export default function Footer({ activeTab, onTabChange }: FooterProps) {
  return (
    <nav className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 shadow-md flex justify-around py-3">
      {tabs.map((tab) => (
        <button
          key={tab.id}
          onClick={() => onTabChange(tab.id)}
          className={`flex-1 text-center py-2 font-semibold transition-colors duration-200 ${
            activeTab === tab.id ? 'text-orange-600 border-t-2 border-orange-600' : 'text-gray-500'
          }`}
          aria-current={activeTab === tab.id ? 'page' : undefined}
          aria-label={`Go to ${tab.label}`}
        >
          {tab.label}
        </button>
      ))}
    </nav>
  )
}
