'use client'

import React, { ReactNode, useState } from 'react'
import { useAuth } from '../context/AuthContext'
import Footer from './Footer'

interface LayoutContentProps {
  children: ReactNode
  showFooter?: boolean
}

export default function LayoutContent({ children, showFooter }: LayoutContentProps) {
  const [activeTab, setActiveTab] = useState('map')
  const { user } = useAuth()
  const shouldShowFooter = showFooter !== undefined ? showFooter : !!user

  return (
    <>
      <main className="flex-grow w-full">{children}</main>
      {shouldShowFooter && <Footer activeTab={activeTab} onTabChange={setActiveTab} />}
    </>
  )
}
